body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
  flex-direction: row;
}

.bar {
  display: flex;
  flex-direction: column;
  width: 210px;
  background-color:black;
  height: 100vh;
  align-items: center;
  float: left;
  position: fixed;
  color: white;
}

html {
  scroll-behavior: smooth;
}

.title {
  display: flex;
  margin-top: 50px;
  font-size: 40px;
  font-family: Gabriola;
}

.profile_pic {
  display: flex;
  margin-top: 30px;
}

.links {
  display: flex;
  margin-top: 8px;
  font-size: 17px;
  height: 10px;
}

.content {
  display: flex;
  flex-direction: column;
}

.Profile, .Experiences, .Projects, .Skills, .Education {
  margin:8px;
  min-width: 180px;
  padding: 10px 0px;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border-radius:10px;
  font-family: Franklin Gothic Medium;
}

.a {
  text-decoration: none;
  color: black;
}

.Profile:hover, .Experiences:hover, .Projects:hover, .Skills:hover, .Education:hover {
  cursor: pointer;
  background-color: lightskyblue;
  color: black;
}

#Profile, #Experiences, #Projects, #Skills, #Education {
  min-height: 100vh;
  min-width: 100vw;
  margin-left: 210px;
}

#Education,  #Skills{
  background-color: AntiqueWhite ;
}

.Profile{
  margin-top:80px;
}

.content-title {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  font-size: 4em;
  font-family: Gabriola;
  margin-left: 5vh;
  padding: 15px;
}

.content-title:before {
  content : "";
  position: absolute;
  width: 65%;
  height: 50px;
  border-bottom:2px groove gray;
}

.content-about {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.about {
  display: flex;
  flex-direction: column;
  margin-left: 5vh;
  margin-top:30vh;
  padding: 15px;
  width: 67vw;
}

.education-college, .education-title {
  display: flex;
  flex-direction: row;
  margin-left: 5vh;
  margin-top:5vh;
  padding: 15px;
  width: 67vw;
}

.education-hs {
  display: flex;
  flex-direction: row;
  margin-left: 5vh;
  margin-top:3vh;
  padding: 15px;
  width: 67vw;
}



.education-title {
  margin-top: 50px;
}

.education-college {
  font-size: 1.5em;
}

p {
  margin-top: 10px;
  font-size: 0.8em;
  line-height: 20pt;
}

.college-intro {
  width: 400px;
}

.college-detail {
  margin-left: 10vw;
  width: 70vw;
}

.about-title, .education-title, .education-hs {
  font-size: 2em;
  color: #22A6BE;
  font-family: Segoe Print;
}

.about-content {
  margin-top: 5vh;
  font-size: 1.5em;
  line-height: 20pt
}

.content-content {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  align-items: center;
}


